.welcome-menu {
  position: absolute;
  height: 500px;
  width: 500px;
  background-color: #b17e24;
  border-radius: 50%;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0.9;
  box-shadow: 0px 2px 29px 12px rgba(86, 86, 86, 0.99);
  -webkit-box-shadow: 0px 2px 29px 12px rgba(109, 109, 109, 0.99);
  -moz-box-shadow: 0px 2px 29px 12px rgba(86, 86, 86, 0.99);
  opacity: 0;
}

.logo {
  position: relative;
  top: 0px;
  opacity: 0;
}

.suggest-button {
  display: -webkit-box;
  margin: auto;
  background-color: white;
  border: 1px solid white;
  position: relative;
  top: 200px;
  height: 12%;
  width: 48%;
  border-radius: 29px;
  font-size: 25px;
  padding-top: 6px;
  color: #bb8008;
  font-weight: 700;
  opacity: 0;
  box-shadow: 0px 3px 62px 7px rgba(148,148,148,1);
  cursor: pointer;
}

.suggest-button:hover {
  box-shadow: 0px 3px 62px 17px rgba(148,148,148,1);
}

.store-icon {
  height: 24px;
}

.play-store-icon {
  margin-right: 12px;
}

.app-store-icon {
  margin-left: 12px;
}

.store-icon-div {
  background-color: #4a290069;
  display: -webkit-inline-box;
  position: relative;
  margin-top: 68px;
  opacity: 0.8;
  height: 38px;
  width: 40px;
  border-radius: 50%;
  padding-top: 15px;
  padding-left: 15px;
  cursor: pointer;
}

.store-icon-div:hover {
  opacity: 1;
}

.rights {
  margin-top: 22px;
  font-size: 13px;
  color: white;
}

.extra-info {
  opacity: 0;
}

.foursquare-icon {
  width: 170px;
  position: fixed;
  bottom: 15px;
  left: calc(50% - 85px);
}

.loading-icon {
  position: relative;
  height: 39px;
  top: 5px;
}

.success-icon {
  position: relative;
  height: 34px;
  top: 6px;
}

@media only screen and (max-width: 768px) {
  .welcome-menu {
    height: 300px;
    width: 300px;
  }  
  .suggest-button {
    font-size: 19px;
    height: 15%;
  }
  .rights {
    position: fixed;
    bottom: 10px;
    width: 170px;
    left: calc(50% - 85px);
  }
  .store-icon-div {
    background-color: #4a290069;
    display: -webkit-inline-box;
    position: relative;
    margin-top: 43px;
    opacity: 0.8;
    height: 29px;
    width: 31px;
    border-radius: 50%;
    padding-top: 12px;
    padding-left: 13px;
    cursor: pointer;
  }

  .foursquare-icon {
    bottom: 33px;
  }

  .store-icon {
    height: 18px;
  }

  .loading-icon {
    position: relative;
    height: 25px;
    top: 3px;
  }

  .success-icon {
    position: relative;
    height: 28px;
    top: 2px;
  }
}
.footer-section {
  position: fixed;
  bottom: -12%;
  /* bottom: 0px; */
  background-color: #c78523c4;
  width: 500px;
  height: 8%;
  left: calc(50% - 250px);
  border-radius: 7px 7px 0px 0px;
  box-shadow: 0px 2px 29px 12px rgba(86, 86, 86, 0.99);
  -webkit-box-shadow: 0px 2px 29px 12px rgba(109, 109, 109, 0.99);
  -moz-box-shadow: 0px 2px 29px 12px rgba(86, 86, 86, 0.99);
}

.footer-suggest-button {
  width: 195px;
  height: 72%;
  position: relative;
  top: 9px;
  font-size: 21px;
  color: #bb8008;
  border-radius: 5px;
  cursor: pointer;
  background: white;
  border: none;
}

.footer-suggest-button:hover {
  box-shadow: 0px 3px 62px 7px rgba(148,148,148,1);
}


.restaurant-main-information {
  display: inline-block;
  height: 100%;
  position: relative;
  bottom: 0px;
  margin-left: 5%;
}

.restaurant-name {
  width: 208px;
  color: #9e6814;
  padding: 0px;
  margin: 0px;
  white-space: nowrap;
  overflow: hidden;
  /* top: 22px; */
}

.rating {
  display: inline-block;
  position: relative;
  left: -23px;
}

.rating-value {
  width: 55px;
  position: relative;
  bottom: 26px;
  left: 99px;
  color: #dab800;
}

.is-open {
  position: relative;
  bottom: 23px;
  background-color: #2eb32e;
  color: white;
  width: 45px;
  border-radius: 17px;
  margin: auto;
}

.is-open-text {
  display: inline-blosck;
}


.is-closed {
  position: relative;
  bottom: 23px;
  background-color: #b32727;
  color: white;
  width: 45px;
  border-radius: 17px;
  margin: auto;
}

.suggestion-body {
  padding: 20px 20px 20px 20px;
  min-width: 326px;
  margin: auto;
  box-shadow: 0px 2px 29px 12px rgba(154, 154, 154, 0.99);
  -webkit-box-shadow: 0px 2px 29px 12px rgba(154, 154, 154, 0.99);
  -moz-box-shadow: 0px 2px 29px 12px rgba(154, 154, 154, 0.99);
  top: -100%;
  /* top: 0px; */
  right: 0;
  left: 0;
  border-radius: 5px;
  width: 84%;
  background-color: white;
  height: calc(92% - 59px);
  position: fixed;
  border-radius: 0px 0px 4px 4px;
  opacity: 0.9;
  max-width: 630PX;
}

.logo2 {
  height: 20%;
  position: relative;
  top: -7px;
}

.restaurant-information-section {
  height: 22%;
  border-radius: 4px;
}

.restaurant-image-div {
  display: inline-block;
  position: absolute;
  height: 100%;
  position: relative;
  margin: auto;
}

.restaurant-image {
  height: 75%;
  border-radius: 50%;
  border: 1px solid black;
}

.map-div {
  display: block;
  height: 245%;
  text-align: start;
  background-color: white;
}

.map-pin {
  height: 50px;
  position: relative;
  bottom: 49px;
  right: 30px;
}

.App {
  text-align: center;
}

button {
  font-family: 'El Messiri', sans-serif;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0px;
  padding: 0px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'El Messiri', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: url('https://wadhahessam.github.io/restaurant-suggestion/img/background3.jpg') no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover; */
  background-image: url("https://wadhahessam.github.io/restaurant-suggestion/img/background3.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center top;
  height:auto;
  max-height: 100%;
  background-color: #5d5d5d;
}
